import { HttpResponse } from '@angular/common/http';
import { ChangeDetectorRef, Component, Directive } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { NotificationService } from '@backbase/ui-ang/notification';
import { EditPostalAddressComponent, ManageProfilePreferencesService } from '@backbase/user-common';
import { ConnectableObservable } from 'rxjs';
import { GetPostalAddress } from '../../types/user-types';
import { UserManageProfileService } from '../../user-manage-profile.service';

@Component({
  selector: 'bb-user-edit-postal-address',
  templateUrl: './user-edit-postal-address.component.html',
})
export class UserEditPostalAddressComponent extends EditPostalAddressComponent {
  constructor(
    private readonly userManageProfileService: UserManageProfileService,
    protected readonly manageProfilePreferencesService: ManageProfilePreferencesService,
    protected readonly cd: ChangeDetectorRef,
    protected readonly notificationService: NotificationService,
  ) {
    super(manageProfilePreferencesService, cd, notificationService);
  }

  handleSaveClick(postalAddressData: GetPostalAddress) {
    this.isSubmitting = true;
    (
      this.userManageProfileService
        .updatePostalAddressRecord({ ...this.postalAddressData, ...postalAddressData })
        .pipe(this.handleUpdateResponse()) as ConnectableObservable<HttpResponse<undefined>>
    ).connect();
  }

  handleDeleteClick(postalAddressData: GetPostalAddress) {
    this.isSubmitting = true;
    (
      this.userManageProfileService
        .deletePostalAddressRecord(postalAddressData)
        .pipe(this.handleDeleteResponse()) as ConnectableObservable<HttpResponse<undefined>>
    ).connect();
  }
}

@Directive({
  selector: '[bbUserEditPostalAddressCustomizable]',
})
export class UserEditPostalAddressCustomizableDirective extends BbTemplate<UserEditPostalAddressComponent, undefined> {}

@Directive({
  selector: '[bbUserEditPostalAddressTitleCustomizable]',
})
export class UserEditPostalAddressTitleCustomizableDirective extends BbTemplate<
  UserEditPostalAddressComponent,
  undefined
> {}
