import { Component, Directive, Input } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { CountryCode } from '@backbase/ui-ang/phone-number-format-pipe';
import { ManagePhoneNumbersComponent } from '@backbase/user-common';

@Component({
  selector: 'bb-user-manage-phone-numbers',
  templateUrl: './user-manage-phone-numbers.component.html',
})
export class UserManagePhoneNumbersComponent extends ManagePhoneNumbersComponent {
  @Input() phoneNumberCountryCode: CountryCode | undefined;
  @Input() phoneNumberDebounceTime: number | undefined;
  @Input() enablePhoneNumberFormatter: boolean | undefined;
}

@Directive({
  selector: '[bbUserManagePhoneNumbersCustomizable]',
})
export class UserManagePhoneNumbersCustomizableDirective extends BbTemplate<
  UserManagePhoneNumbersComponent,
  undefined
> {}

@Directive({
  selector: '[bbUserManagePhoneNumbersTitleCustomizable]',
})
export class UserManagePhoneNumbersTitleCustomizableDirective extends BbTemplate<
  UserManagePhoneNumbersComponent,
  undefined
> {}
