import { HttpResponse } from '@angular/common/http';
import { ChangeDetectorRef, Component, Directive, Input } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { NotificationService } from '@backbase/ui-ang/notification';
import { CountryCode } from '@backbase/ui-ang/phone-number-format-pipe';
import { EditPhoneNumberComponent, ManageProfilePreferencesService } from '@backbase/user-common';
import { ConnectableObservable } from 'rxjs';
import { GetPhoneAddress } from '../../types/user-types';
import { UserManageProfileService } from '../../user-manage-profile.service';

@Component({
  selector: 'bb-user-edit-phone-number',
  templateUrl: './user-edit-phone-number.component.html',
})
export class UserEditPhoneNumberComponent extends EditPhoneNumberComponent {
  @Input() phoneNumberCountryCode: CountryCode | undefined;
  @Input() phoneNumberDebounceTime: number | undefined;
  @Input() enablePhoneNumberFormatter: boolean | undefined;

  constructor(
    private readonly manageProfileService: UserManageProfileService,
    protected readonly manageProfilePreferencesService: ManageProfilePreferencesService,
    protected readonly cd: ChangeDetectorRef,
    protected readonly notificationService: NotificationService,
  ) {
    super(manageProfilePreferencesService, cd, notificationService);
  }

  handleSaveClick(phoneNumberData: GetPhoneAddress) {
    this.isSubmitting = true;
    (
      this.manageProfileService
        .updatePhoneAddressRecord(phoneNumberData)
        .pipe(this.handleUpdateResponse()) as ConnectableObservable<HttpResponse<undefined>>
    ).connect();
  }

  handleDeleteClick(phoneNumberData: GetPhoneAddress) {
    this.isSubmitting = true;
    (
      this.manageProfileService
        .deletePhoneAddressRecord(phoneNumberData)
        .pipe(this.handleDeleteResponse()) as ConnectableObservable<HttpResponse<undefined>>
    ).connect();
  }
}

@Directive({
  selector: '[bbUserEditPhoneNumberCustomizable]',
})
export class UserEditPhoneNumberCustomizableDirective extends BbTemplate<UserEditPhoneNumberComponent, undefined> {}

@Directive({
  selector: '[bbUserEditPhoneNumberTitleCustomizable]',
})
export class UserEditPhoneNumberTitleCustomizableDirective extends BbTemplate<
  UserEditPhoneNumberComponent,
  undefined
> {}
