import { HttpResponse } from '@angular/common/http';
import { ChangeDetectorRef, Component, Directive } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { NotificationService } from '@backbase/ui-ang/notification';
import { ManageProfilePreferencesService, NewPostalAddressComponent } from '@backbase/user-common';
import { ConnectableObservable } from 'rxjs';
import { PostalAddress } from '../../types/user-types';
import { UserManageProfileService } from '../../user-manage-profile.service';

@Component({
  selector: 'bb-user-new-postal-address',
  templateUrl: './user-new-postal-address.component.html',
})
export class UserNewPostalAddressComponent extends NewPostalAddressComponent {
  constructor(
    private readonly manageProfileService: UserManageProfileService,
    protected readonly manageProfilePreferencesService: ManageProfilePreferencesService,
    protected readonly cd: ChangeDetectorRef,
    protected readonly notificationService: NotificationService,
  ) {
    super(manageProfilePreferencesService, cd, notificationService);
  }

  handleSaveClick(postalAddressData: PostalAddress) {
    this.isSubmitting = true;
    (
      this.manageProfileService
        .createPostalAddressRecord(postalAddressData)
        .pipe(this.handleServiceResponse()) as ConnectableObservable<HttpResponse<undefined>>
    ).connect();
  }
}

@Directive({
  selector: '[bbUserNewPostalAddressCustomizable]',
})
export class UserNewPostalAddressCustomizableDirective extends BbTemplate<UserNewPostalAddressComponent, undefined> {}

@Directive({
  selector: '[bbUserNewPostalAddressTitleCustomizable]',
})
export class UserNewPostalAddressTitleCustomizableDirective extends BbTemplate<
  UserNewPostalAddressComponent,
  undefined
> {}
