import { ChangeDetectorRef, Component, Directive, Input, OnInit } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { CountryCode } from '@backbase/phone-number-lib';
import {
  convertCommaSeparatedStringToStringArray,
  filterInputParameters,
  ManageProfileComponent,
  ManageProfilePreferencesService,
  ManageProfileService,
  PhoneNumberInputPreferences,
} from '@backbase/user-common';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { catchError, first, pluck, switchMap, tap } from 'rxjs/operators';
import { UserProfile } from './types/user-types';
import { UserManageProfileService } from './user-manage-profile.service';

@Component({
  selector: 'bb-user-manage-profile-widget',
  templateUrl: './user-manage-profile-widget.component.html',
  providers: [UserManageProfileService, ManageProfilePreferencesService, ManageProfileService],
})
export class UserManageProfileWidgetComponent extends ManageProfileComponent implements OnInit {
  public dismissTimeout: number | undefined;

  @Input('emailAddressTypes') set emailAddressTypesValue(value: Array<string> | string) {
    this.emailAddressTypesSubject.next(
      typeof value === 'string' ? this.convertCommaSeparatedStringToStringArray(value) : value,
    );
  }
  @Input('postalAddressTypes') set postalAddressTypesValue(value: Array<string> | string) {
    this.postalAddressTypesSubject.next(
      typeof value === 'string' ? this.convertCommaSeparatedStringToStringArray(value) : value,
    );
  }
  @Input('phoneNumberTypes') set phoneNumberTypesValue(value: Array<string> | string) {
    this.phoneNumberTypesSubject.next(
      typeof value === 'string' ? this.convertCommaSeparatedStringToStringArray(value) : value,
    );
  }
  @Input('maxEmailAddresses') set maxEmailAddressesValue(value: number) {
    this.maxEmailAddressesSubject.next(value);
  }
  @Input('maxPhoneNumbers') set maxPhoneNumbersValue(value: number) {
    this.maxPhoneNumbersSubject.next(value);
  }
  @Input('maxPostalAddresses') set maxPostalAddressesValue(value: number) {
    this.maxPostalAddressesSubject.next(value);
  }
  @Input('showWidgetHeader') set showWidgetHeaderValue(value: boolean | string) {
    this.showWidgetHeaderSubject.next(typeof value === 'string' ? !!value && value !== 'false' : value);
  }
  @Input('phoneNumberCountryCode') set phoneNumberCountryCodeValue(value: CountryCode) {
    this.phoneNumberCountryCodeSubject.next(value);
  }
  @Input('phoneNumberDebounceTime') set phoneNumberDebounceTimeValue(value: number) {
    this.phoneNumberDebounceTimeSubject.next(value);
  }
  @Input('enablePhoneNumberFormatter') set enablePhoneNumberFormatterValue(value: boolean | string) {
    this.enablePhoneNumberFormatterSubject.next(typeof value === 'string' ? value === 'true' : value);
  }

  private readonly emailAddressTypesSubject = new BehaviorSubject<Array<string> | undefined>(undefined);
  private readonly postalAddressTypesSubject = new BehaviorSubject<Array<string> | undefined>(undefined);
  private readonly phoneNumberTypesSubject = new BehaviorSubject<Array<string> | undefined>(undefined);
  private readonly maxEmailAddressesSubject = new BehaviorSubject<number | undefined>(undefined);
  private readonly maxPhoneNumbersSubject = new BehaviorSubject<number | undefined>(undefined);
  private readonly maxPostalAddressesSubject = new BehaviorSubject<number | undefined>(undefined);
  private readonly showWidgetHeaderSubject = new BehaviorSubject<boolean | undefined>(undefined);
  private readonly phoneNumberCountryCodeSubject = new BehaviorSubject<CountryCode | undefined>(undefined);
  private readonly phoneNumberDebounceTimeSubject = new BehaviorSubject<number | undefined>(undefined);
  private readonly enablePhoneNumberFormatterSubject = new BehaviorSubject<boolean | undefined>(undefined);

  readonly emailAddressTypes: Observable<Array<string>> = filterInputParameters(
    this.emailAddressTypesSubject.asObservable(),
    this.manageProfilePreferencesService.getWidgetPreferences().pipe(pluck('emailAddressTypes')),
  );
  readonly postalAddressTypes: Observable<Array<string>> = filterInputParameters(
    this.postalAddressTypesSubject.asObservable(),
    this.manageProfilePreferencesService.getWidgetPreferences().pipe(pluck('postalAddressTypes')),
  );
  readonly phoneNumberTypes: Observable<Array<string>> = filterInputParameters(
    this.phoneNumberTypesSubject.asObservable(),
    this.manageProfilePreferencesService.getWidgetPreferences().pipe(pluck('phoneNumberTypes')),
  );
  readonly maxEmailAddresses: Observable<number> = filterInputParameters(
    this.maxEmailAddressesSubject.asObservable(),
    this.manageProfilePreferencesService.getWidgetPreferences().pipe(pluck('maxEmailAddresses')),
  );
  readonly maxPhoneNumbers: Observable<number> = filterInputParameters(
    this.maxPhoneNumbersSubject.asObservable(),
    this.manageProfilePreferencesService.getWidgetPreferences().pipe(pluck('maxPhoneNumbers')),
  );
  readonly maxPostalAddresses: Observable<number> = filterInputParameters(
    this.maxPostalAddressesSubject.asObservable(),
    this.manageProfilePreferencesService.getWidgetPreferences().pipe(pluck('maxPostalAddresses')),
  );
  readonly showWidgetHeader: Observable<boolean> = filterInputParameters(
    this.showWidgetHeaderSubject.asObservable(),
    this.manageProfilePreferencesService.getWidgetPreferences().pipe(pluck('showWidgetHeader')),
  );
  readonly phoneNumberCountryCode: Observable<CountryCode> = filterInputParameters(
    this.phoneNumberCountryCodeSubject.asObservable(),
    this.manageProfilePreferencesService.getPhoneNumberInputPreferences().pipe(pluck('countryCode')),
  );
  readonly phoneNumberDebounceTime: Observable<number> = filterInputParameters(
    this.phoneNumberDebounceTimeSubject.asObservable(),
    this.manageProfilePreferencesService.getPhoneNumberInputPreferences().pipe(pluck('debounceTime')),
  );
  readonly enablePhoneNumberFormatter: Observable<boolean> = filterInputParameters(
    this.enablePhoneNumberFormatterSubject.asObservable(),
    this.manageProfilePreferencesService
      .getPhoneNumberInputPreferences()
      .pipe(
        switchMap((preferences: PhoneNumberInputPreferences | undefined) =>
          of((preferences && preferences.enablePhoneNumberFormatter) || false),
        ),
      ),
  );

  constructor(
    private readonly manageProfileService: UserManageProfileService,
    protected readonly manageProfilePreferencesService: ManageProfilePreferencesService,
    protected readonly cd: ChangeDetectorRef,
  ) {
    super(manageProfilePreferencesService, cd);
  }

  ngOnInit() {
    super.ngOnInit();

    if (this.notificationDismissTime) {
      this.dismissTimeout = this.notificationDismissTime * 1000;
    } else {
      this.manageProfilePreferencesService
        .getNotificationDismissTimePreference()
        .pipe(first())
        .subscribe((time) => (this.dismissTimeout = time * 1000));
    }
  }

  public convertCommaSeparatedStringToStringArray(value: string) {
    return convertCommaSeparatedStringToStringArray(value);
  }

  protected setUserProfileObservable(): Observable<UserProfile> {
    return this.userProfileSubject.asObservable().pipe(
      tap(this.onProfileUpdate),
      switchMap(() => this.manageProfileService.getUserProfile()),
      tap(() => (this.loadingState = false)),
      catchError(this.handleProfileError),
    );
  }
}

@Directive({
  selector: '[bbUserManageWidgetCustomizable]',
})
export class UserManageWidgetCustomizableDirective extends BbTemplate<UserManageProfileWidgetComponent, undefined> {}

@Directive({
  selector: '[bbUserManageWidgetTitleCustomizable]',
})
export class UserManageWidgetTitleCustomizableDirective extends BbTemplate<
  UserManageProfileWidgetComponent,
  undefined
> {}
