import { HttpResponse } from '@angular/common/http';
import { ChangeDetectorRef, Component, Directive } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { NotificationService } from '@backbase/ui-ang/notification';
import { EditEmailAddressComponent, ManageProfilePreferencesService } from '@backbase/user-common';
import { ConnectableObservable } from 'rxjs';
import { GetElectronicAddress } from '../../types/user-types';
import { UserManageProfileService } from '../../user-manage-profile.service';

@Component({
  selector: 'bb-user-edit-email-address',
  templateUrl: './user-edit-email-address.component.html',
})
export class UserEditEmailAddressComponent extends EditEmailAddressComponent {
  constructor(
    private readonly manageProfileService: UserManageProfileService,
    protected readonly manageProfilePreferencesService: ManageProfilePreferencesService,
    protected readonly cd: ChangeDetectorRef,
    protected readonly notificationService: NotificationService,
  ) {
    super(manageProfilePreferencesService, cd, notificationService);
  }

  handleSaveClick(emailAddressData: GetElectronicAddress) {
    this.isSubmitting = true;
    (
      this.manageProfileService
        .updateElectronicAddressRecord(emailAddressData)
        .pipe(this.handleUpdateResponse()) as ConnectableObservable<HttpResponse<undefined>>
    ).connect();
  }

  handleDeleteClick(emailAddressData: GetElectronicAddress) {
    this.isSubmitting = true;
    (
      this.manageProfileService
        .deleteElectronicAddressRecord(emailAddressData)
        .pipe(this.handleDeleteResponse()) as ConnectableObservable<HttpResponse<undefined>>
    ).connect();
  }
}

@Directive({
  selector: '[bbUserEditEmailAddressCustomizable]',
})
export class UserEditEmailAddressCustomizableDirective extends BbTemplate<UserEditEmailAddressComponent, undefined> {}

@Directive({
  selector: '[bbUserEditEmailAddressTitleCustomizable]',
})
export class UserEditEmailAddressTitleCustomizableDirective extends BbTemplate<
  UserEditEmailAddressComponent,
  undefined
> {}
