import { Component, Directive, Input } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { CountryCode } from '@backbase/ui-ang/phone-number-format-pipe';
import { ManagePhoneNumberComponent } from '@backbase/user-common';

@Component({
  selector: 'bb-user-manage-phone-number',
  templateUrl: './user-manage-phone-number.component.html',
})
export class UserManagePhoneNumberComponent extends ManagePhoneNumberComponent {
  @Input() phoneNumberCountryCode: CountryCode | undefined;
  @Input() phoneNumberDebounceTime: number | undefined;
  @Input() enablePhoneNumberFormatter: boolean | undefined;
}

@Directive({
  selector: '[bbUserManagePhoneNumberCustomizable]',
})
export class UserManagePhoneNumberCustomizableDirective extends BbTemplate<UserManagePhoneNumberComponent, undefined> {}
