import { Component, Directive, Input } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { CountryCode } from '@backbase/ui-ang/phone-number-format-pipe';
import { ManageProfilePhoneNumberFormComponent } from '@backbase/user-common';

@Component({
  selector: 'bb-user-phone-number-form',
  templateUrl: './user-phone-number-form.component.html',
})
export class UserPhoneNumberFormComponent extends ManageProfilePhoneNumberFormComponent {
  @Input() phoneNumberCountryCode: CountryCode | undefined;
  @Input() phoneNumberDebounceTime: number | undefined;
  @Input() enablePhoneNumberFormatter: boolean | undefined;
}

@Directive({
  selector: '[bbUserPhoneNumberFormCustomizable]',
})
export class UserPhoneNumberFormCustomizableDirective extends BbTemplate<UserPhoneNumberFormComponent, undefined> {}

@Directive({
  selector: '[bbUserPhoneNumberFormErrorsCustomizable]',
})
export class UserPhoneNumberFormErrorsCustomizableDirective extends BbTemplate<
  UserPhoneNumberFormComponent,
  undefined
> {}
