import { Injectable } from '@angular/core';
import {
  ElectronicAddress,
  GetElectronicAddress,
  GetPhoneAddress,
  GetPostalAddress,
  PhoneAddress,
  PostalAddress,
  UserProfile,
  UserProfileManagementService,
} from '@backbase/data-ang/user';
import { ManageProfileService } from '@backbase/user-common';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable()
export class UserManageProfileService {
  constructor(
    private readonly data: UserProfileManagementService,
    private readonly manageProfileService: ManageProfileService,
  ) {}

  getUserProfile(): Observable<UserProfile> {
    return this.data.getOwnUserProfile().pipe(map(this.manageProfileService.sortUserContactListsByPrimary));
  }

  createElectronicAddressRecord(electronicAddress: ElectronicAddress): Observable<any> {
    return this.data.addOwnElectronicAddress({ electronicAddress });
  }

  updateElectronicAddressRecord(electronicAddress: GetElectronicAddress): Observable<any> {
    const { key, ...record } = electronicAddress;

    return this.data.updateOwnElectronicAddress({
      electronicAddressKey: electronicAddress.key,
      electronicAddress: record,
    });
  }

  deleteElectronicAddressRecord(electronicAddress: GetElectronicAddress): Observable<any> {
    return this.data.deleteOwnElectronicAddress({ electronicAddressKey: electronicAddress.key });
  }

  createPhoneAddressRecord(phoneAddress: PhoneAddress): Observable<any> {
    return this.data.addOwnPhoneAddress({ phoneAddress });
  }

  updatePhoneAddressRecord(phoneAddress: GetPhoneAddress): Observable<any> {
    const { key, ...record } = phoneAddress;

    return this.data.updateOwnPhoneAddress({
      phoneAddressKey: phoneAddress.key,
      phoneAddress: record,
    });
  }

  deletePhoneAddressRecord(phoneAddress: GetPhoneAddress): Observable<any> {
    return this.data.deleteOwnPhoneAddress({ phoneAddressKey: phoneAddress.key });
  }

  createPostalAddressRecord(postalAddress: PostalAddress): Observable<any> {
    return this.data.addOwnPostalAddress({ postalAddress });
  }

  updatePostalAddressRecord(postalAddress: GetPostalAddress): Observable<any> {
    const { key, ...record } = postalAddress;

    return this.data.updateOwnPostalAddress({
      postalAddressKey: postalAddress.key,
      postalAddress: record,
    });
  }

  deletePostalAddressRecord(postalAddress: GetPostalAddress): Observable<any> {
    return this.data.deleteOwnPostalAddress({ postalAddressKey: postalAddress.key });
  }
}
