import { Component, Directive, Input, OnInit } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { InputBase } from '@backbase/user-common';

@Component({
  selector: 'bb-user-primary-checkbox-input',
  templateUrl: './user-primary-checkbox-input.component.html',
})
export class UserPrimaryCheckboxInputComponent extends InputBase implements OnInit {
  /**
   * The form's address type.
   * Used to determine which label to show.
   */
  @Input() type: string | undefined;

  id: string | undefined;
  availableTypes = {
    address: 'postalAddress',
    phone: 'phoneNumber',
    email: 'emailAddress',
  };

  ngOnInit(): void {
    this.id = this.generateId('email-checkbox');
  }

  private generateId(elementKey: string): string {
    return `${elementKey}-${Math.random().toString(36).substring(2, 9)}`;
  }
}

@Directive({
  selector: '[bbUserPrimaryCheckboxInputCustomizable]',
})
export class UserPrimaryCheckboxInputCustomizableDirective extends BbTemplate<
  UserPrimaryCheckboxInputComponent,
  undefined
> {}
